import React, {Component} from 'react';
import {Col, Row, Container} from 'reactstrap';
import Header from '../header';
import RandomChar from '../randomChar';
import Button from 'reactstrap/lib/Button';
import ErrorMessage from '../errorMessage';
import {CharacterPage, HousesPage, BooksPage, BooksItem} from '../pages';
import GotService from '../../services/gotServices';
import {BrowserRouter as Router, Route} from 'react-router-dom';

export default class App extends Component {

    gotService = new GotService();

    state = {
        hidden: true,
        error: false
    } 
 
    onToggle = () => {
        this.setState((state) => {
            return {
                hidden: ! state.hidden
            }
        });
    }

    componentDidCatch() {
        console.log('error');
        this.setState({
            error: true
        })
    }

    render() {
        const random = this.state.hidden ? <RandomChar interval={15000}/> : null;
        
        if (this.state.error) {
            return <ErrorMessage/>
        }

        return (
            <Router>
                <div className="app"> 
                    <Container>
                        <Header />
                    </Container>
                    <Container>
                        <Row>
                            <Col lg={{size: 5, offset: 0}}>
                                {random}
                                <Button className="mb-5" onClick={this.onToggle}>Toggle random Caracter</Button>
                            </Col>
                        </Row>
                        <Route path='/' exact component={() => <h2>Welcome GOT DB</h2>}/>
                        <Route path='/characters' component={CharacterPage}/>
                        <Route path='/houses' component={HousesPage}/>
                        <Route path='/books' exact component={BooksPage}/>
                        <Route path='/books/:id' render={
                            ({match}) => {
                                const {id} = match.params;
                                return <BooksItem bookId={id}/>
                            }
                        }/>
                    </Container>
                </div>
            </Router>
        )
    }
}

