

export default class GotService {
    constructor(){
        this._apiBase = 'https://www.anapioficeandfire.com/api';
    }

    getResource = async (url) =>{
        const res = await fetch(`${this._apiBase}${url}`);
        if (!res.ok) {
            throw new Error(`Cloud not fetch ${url}, status: ${res.status}`);
        }    
        return await res.json();
    }
    getAllCharacters = async () => {
        const res = await this.getResource('/characters?page=5');
        return res.map(this._transformCharacter);
    }

    getCharachter = async (id) => {
        const res = await this.getResource(`/characters/${id}`);
        return this._transformCharacter(res);
    }

    getAllHouses = async () => {
        const res = await this.getResource(`/houses/`);
        return res.map(this._transformHouses);
    }

    getHouse = async (id) => {
        const res = await this.getResource(`/houses/${id}`);
        return this._transformHouses(res);
    }

    getAllBooks = async () => {
        const res = await this.getResource(`/books/`);
        return res.map(this._transformBook);

    }

    getBook = async (id) => {
        const res = await this.getResource(`/books/${id}`);
        return this._transformBook(res);
    }

    onTrue(obj) {
        for( let key in obj) {
            console.log(key);
        }
    }

    _transformCharacter(char) {
        for( let key in char) {
            if(!char[key]) {
              char[key] = 'Undefine'; 
            }
        }
        const getID =/\/([0-9]*)$/;        
        return {
            name: char.name,
            gender: char.gender,
            born: char.born,
            died: char.died,
            culture: char.culture,
            id: char.url.match(getID)[1]
        }
    }
    
    _transformHouses(house) {
        for( let key in house) {
            if(!house[key]) {
                house[key] = 'No Data'; 
            }
        }
        const getID =/\/([0-9]*)$/;     
        return {
            name: house.name,
            region: house.region,
            words: house.words,
            culture: house.culture,
            titles: house.titles,
            overlord: house.overlord,
            ancestralWeapons: house.ancestralWeapons,
            id: house.url.match(getID)[1]
        }
    }

    _transformBook(book) {
        for( let key in book) {
            if(!book[key]) {
                book[key] = 'No Data'; 
            }
        }
        const getID =/\/([0-9]*)$/;  
        return {
            name: book.name,
            numberOfPages: book.numberOfPages,
            publisher: book.publisher,
            released: book.released,
            id: book.url.match(getID)[1]
        }
    }
}
